import React, {
    PureComponent,
    PropTypes,
} from 'react';
import * as AUI from 'aui-react';

import {
    DATA_ROUTE,
    JIRA_HOME_ROUTE,
    ACK_WARNINGS_ROUTE,
    DATA_REFRESH_INTERVAL,
    FIND_THE_LOGS,
    ADD_AND_REMOVE_SYSTEM_PROPERTIES,
} from './constants';

import {
    translate,
} from './translate';
import * as johnsonEvents from './johnson-events';
import * as api from './api';
import OldEventTable from './old_components/Table/Table';

import SubHeader from './components/SubHeader/SubHeader';
import Loader from './components/Loader/Loader';
import FlagContainer from './components/Flag/FlagContainer';
import Flag from './components/Flag/Flag';
import Spinner from './components/Spinner/Spinner';
import PageContent, {
    eventsByTypeProps,
    statsProps,
} from './components/PageContent/PageContent';

import '@atlassian/aui/lib/css/aui-reset.css';
import 'aui-adg/aui/css/aui.css';
import 'aui-adg/aui/css/aui-experimental.css';
import styles from './styles/styles.less';
import {stringify} from 'querystring';
import {sendKBLinkClick} from "./analytics";
export class App extends PureComponent {

    static propTypes = {
        loading: PropTypes.bool.isRequired,
        oldEvents: PropTypes.arrayOf(PropTypes.array),
        events: eventsByTypeProps,
        stats: statsProps,
        hasDismissibleWarnings: PropTypes.bool,
        checksComplete: PropTypes.bool,
        canAcknowledgeWarnings: PropTypes.bool,
        errorsPresentButHidden: PropTypes.bool,
        acknowledgeWarnings: PropTypes.string,
    };

    static contextTypes = {
        i18n: PropTypes.func,
    };

    getSubHeader(hasErrors, i18n) {
        if (hasErrors) {
            return (
                <SubHeader headline={i18n('johnson.page.headline.error')}>
                    {i18n('johnson.page.subheadline.error')}
                </SubHeader>
            )
        }

        return (
            <SubHeader headline={i18n('johnson.page.headline.neutral')}>
                <span
                    dangerouslySetInnerHTML={{__html: i18n('johnson.page.subheadline.neutral', `<a href="${JIRA_HOME_ROUTE}">`, '</a>')}}/>
            </SubHeader>
        )
    }



    getLink(i18n, link) {
        return (<a onClick={sendKBLinkClick} target="_blank"
                   href={link}>{i18n("johnson.check.events.event.kblink")}</a>);
    }

    getIntro(hasErrors, i18n, errorsPresentButHidden) {
        if (errorsPresentButHidden) {
            return (
                <AUI.PageContent>
                    <p>
                        <span dangerouslySetInnerHTML={{__html: i18n('johnson.page.detail.restricted.intro') + " "}}/>
                        { this.getLink(i18n, FIND_THE_LOGS )}
                    </p>

                </AUI.PageContent>
            )
        }
        return null;
    }

    getOutro(hasErrors, i18n, errorsPresentButHidden) {
        if (errorsPresentButHidden) {
            return (
                <AUI.PageContent>
                    <p>
                        <span dangerouslySetInnerHTML={{__html: i18n('johnson.page.detail.restricted.outro') + " "}}/>
                        { this.getLink(i18n, ADD_AND_REMOVE_SYSTEM_PROPERTIES)}
                    </p>
                </AUI.PageContent>
            )
        }

        if (!hasErrors) {
            return null;
        }

        return (
            <AUI.PageContent>
                <p dangerouslySetInnerHTML={{__html: i18n('johnson.page.outro', `<a href="${FIND_THE_LOGS}" target="_blank">`, '</a>')}}/>
            </AUI.PageContent>
        )
    }

    getBetterJohnsonEvents(hasBetterJohnsonErrors, props) {
        if (!hasBetterJohnsonErrors) {
            return null;
        }

        return <PageContent stats={props.stats} hasDismissibleWarnings={props.hasDismissibleWarnings}
                            canAcknowledgeWarnings={props.canAcknowledgeWarnings} eventsByType={props.events}
                            acknowledgeWarnings={ACK_WARNINGS_ROUTE}/>
    }

    getLegacyJohnsonEvents(hasLegacyJohnsonErrors, props) {
        if (!hasLegacyJohnsonErrors) {
            return null;
        }

        return (
            <AUI.PageContent>
                <OldEventTable events={props.oldEvents}/>
            </AUI.PageContent>
        );
    }

    getContent() {
        const {i18n} = this.context;
        const props = this.props;

        const hasBetterJohnsonErrors = props.stats.total > 0;
        const hasLegacyJohnsonErrors = props.oldEvents.length > 0;
        const hasErrors = hasBetterJohnsonErrors || hasLegacyJohnsonErrors || props.errorsPresentButHidden;

        return (
            <AUI.PageContent className={styles.mainContent}>
                {this.getSubHeader(hasErrors, i18n)}
                {this.getIntro(hasErrors, i18n, props.errorsPresentButHidden)}
                {this.getBetterJohnsonEvents(hasBetterJohnsonErrors, props)}
                {this.getLegacyJohnsonEvents(hasLegacyJohnsonErrors, props)}
                {this.checksInProgressIndicator()}
                {this.getOutro(hasErrors, i18n, props.errorsPresentButHidden)}
            </AUI.PageContent>
        );
    }

    checksInProgressIndicator() {
        if (this.props.checksComplete) {
            return null;
        }

        const {i18n} = this.context;
        return (
            <div className={styles.checkProgress}>
                <h4 className={styles.checkProgressTitle}>{i18n("johnson.page.progress")}</h4>
                <Spinner />
            </div>
        );
    }

    getLoader() {
        return (
            <AUI.PageContent>
                <Loader/>
            </AUI.PageContent>
        );
    }

    noAdminFlag() {
        if (!this.props.showNoAdminFlag) {
            return null;
        }
        return (
            <FlagContainer>
                <Flag>
                    {flag => (
                        <AUI.Message
                            type="error"
                            title={this.context.i18n("johnson.warnings.acknowledge.error.title")}
                            isCloseable
                            onClose={() => {
                                flag.hide();
                                location.hash = ""; // reset hash so refresh doesnt trigger flag again
                            }}>
                            {this.context.i18n("johnson.warnings.acknowledge.error.no.perm")}
                        </AUI.Message>
                    )}
                </Flag>
            </FlagContainer>
        );
    }

    render() {
        const isLoading = this.props.loading;
        const content = isLoading ? this.getLoader() : this.getContent();
        return (
            <div>
                <AUI.ApplicationHeader logo="jira"/>
                {content}
                {this.noAdminFlag()}
            </div>
        );
    }
}

export default ((App) => class extends PureComponent {

    static childContextTypes = {
        i18n: PropTypes.func,
        refreshFn: PropTypes.func,
    };

    constructor() {
        super();

        if (typeof window !== "undefined") {
            if (window.i18n) {
                this.i18nMap = window.i18n;
            }
            if (window.initialData) {
                this.state = this.calculateState(window.initialData);
                return;
            }
        }

        this.state = {
            loading: true,
        };
    }

    getChildContext() {
        return {
            i18n: this.i18n,
            refreshFn: this.fetchData
        };
    }

    i18nMap = null;
    i18n = (...args) => translate(this.i18nMap, ...args);

    calculateState(payload) {
        const sortedEvents = johnsonEvents.sortByEvent(payload.events);
        const eventsByType = johnsonEvents.sortByType(sortedEvents.events);
        const eventStats = johnsonEvents.stats(eventsByType);
        const oldEventsDataRows = johnsonEvents.oldEventsToTableRowArrays(
            sortedEvents.oldEvents,
            this.i18n
        );

        return {
            loading: false,
            oldEvents: oldEventsDataRows,
            events: eventsByType,
            stats: eventStats,
            hasDismissibleWarnings: payload.hasDismissibleWarnings,
            checksComplete: payload.checksComplete,
            canAcknowledgeWarnings: payload.canAcknowledgeWarnings,
            acknowledgeWarnings: payload.acknowledgeWarnings,
            showNoAdminFlag: location.hash === '#no-admin',
            errorsPresentButHidden: payload.errorsPresentButHidden
        };
    }

    fetchData = async() => {
        try {
            const jsonPayload = await api.getData(DATA_ROUTE);
            const newState = this.calculateState(jsonPayload);
            this.setState({
                ...this.state,
                ...newState,
            });
        } finally {
            // make sure we keep the fetch cycle alive
            setTimeout(this.fetchData, DATA_REFRESH_INTERVAL);
        }
    };

    componentDidMount() {
        this.fetchData();
    }

    render() {
        return (<App {...this.state}/>);
    }
})(App);


