export const getData = (route, transporter = fetch) => {
    if(process.env.NODE_ENV === 'development' && process.env.REACT_APP_MOCK_DATA) {
        const mockData = require('./mocks/mock-data');
        const checksComplete = Math.random() > 0.1;
        return Promise.resolve({
            hasDismissibleWarnings: checksComplete && Math.random() > 0.3,
            canAcknowledgeWarnings: checksComplete && Math.random() > 0.5,
            checksComplete: checksComplete,
            events: [
                ...mockData.generateEvents(mockData.getRandomIssueNumber()),
                ...mockData.generateOldEvents(mockData.getRandomIssueNumber())
            ]
        });
    }

    return transporter(route).then(result => result.json());
};

export const flatObjectToEncodedFormData = (obj) => Object.keys(obj).map(key => `${encodeURIComponent(key)}=${encodeURIComponent(obj[key])}`).join("&");


