import React, {PureComponent} from 'react';

export default class Flag extends PureComponent {

    constructor() {
        super();
        this.state = {
            rendered: null,
        };
    }

    hide() {
        this.setState({
            rendered: true,
        });
    }

    componentDidMount() {
        setTimeout(() => {
            this.setState({
                rendered: false,
            });
        }, 100);
    }

    render() {
        return (
            <div className="aui-flag" aria-hidden={this.state.rendered}>
                {this.props.children(this)}
            </div>
        );
    }
}




