import React, {PureComponent} from 'react';
import classnames from 'classnames';

import styles from './spinner.less';

/**
 * react port of AUI-Spinner
 * see https://docs.atlassian.com/aui/latest/docs/spinner.html
 */
export default class extends PureComponent {
    render() {
        return (<div>
            <div className={styles.spinner} role="progressbar">
                <div className={classnames(styles.spinnerHandWrapper, styles.spinnerHandWrapper1)}>
                    <div className={styles.spinnerHand}>
                    </div>
                </div>
                <div className={classnames(styles.spinnerHandWrapper, styles.spinnerHandWrapper2)}>
                    <div className={styles.spinnerHand}>
                    </div>
                </div>
                <div className={classnames(styles.spinnerHandWrapper, styles.spinnerHandWrapper3)}>
                    <div className={styles.spinnerHand}>
                    </div>
                </div>
                <div className={classnames(styles.spinnerHandWrapper, styles.spinnerHandWrapper4)}>
                    <div className={styles.spinnerHand}>
                    </div>
                </div>
                <div className={classnames(styles.spinnerHandWrapper, styles.spinnerHandWrapper5)}>
                    <div className={styles.spinnerHand}>
                    </div>
                </div>
                <div className={classnames(styles.spinnerHandWrapper, styles.spinnerHandWrapper6)}>
                    <div className={styles.spinnerHand}>
                    </div>
                </div>
                <div className={classnames(styles.spinnerHandWrapper, styles.spinnerHandWrapper7)}>
                    <div className={styles.spinnerHand}>
                    </div>
                </div>
                <div className={classnames(styles.spinnerHandWrapper, styles.spinnerHandWrapper8)}>
                    <div className={styles.spinnerHand}>
                    </div>
                </div>
                <div className={classnames(styles.spinnerHandWrapper, styles.spinnerHandWrapper9)}>
                    <div className={styles.spinnerHand}>
                    </div>
                </div>
                <div className={classnames(styles.spinnerHandWrapper, styles.spinnerHandWrapper10)}>
                    <div className={styles.spinnerHand}>
                    </div>
                </div>
                <div className={classnames(styles.spinnerHandWrapper, styles.spinnerHandWrapper11)}>
                    <div className={styles.spinnerHand}>
                    </div>
                </div>
                <div className={classnames(styles.spinnerHandWrapper, styles.spinnerHandWrapper12)}>
                    <div className={styles.spinnerHand}>
                    </div>
                </div>
            </div>
        </div>);
    }
}


