import {stringify} from 'querystring';
import {ANALYTICS_ROUTE, ANALYTICS_ROUTE_KB_ARTICLE} from './constants';

export const sendHelpLinkClick = (eventId, transporter = fetch) => transporter(`${ANALYTICS_ROUTE}?${stringify({eventId})}`, {method: 'POST'});
export const sendKBLinkClick = ( transporter = fetch) => transporter(`${ANALYTICS_ROUTE_KB_ARTICLE}`, {method: 'POST'});



