import React, {PureComponent, PropTypes} from 'react';
import * as AUI from 'aui-react';
import classnames from 'classnames';

import {sendHelpLinkClick} from '../../analytics';
import styles from './row.less';

export const propTypes = {
    title: PropTypes.string.isRequired,
    description: PropTypes.string,
    moreText: PropTypes.string,
    type: PropTypes.oneOf(['error', 'fatal', 'warning', 'success']).isRequired,
    helpLink: PropTypes.string,
    helpLinkText: PropTypes.string,
    eventId: PropTypes.string,
};

export default class Row extends PureComponent {
    static propTypes = propTypes;

    onHelpLinkClick = () => sendHelpLinkClick(this.props.eventId);

    getLink (helpLink, helpLinkText, classNames) {
        if (!helpLink) {
            return null;
        }
        return (<a className={classNames} onClick={this.onHelpLinkClick} target="_blank" href={helpLink}>{helpLinkText}</a>);
    }

    getClassByType (type) {
        switch (type) {
            case 'warning':
                return styles.warning;
            case 'success':
                return styles.success;
            default:
                return '';
        }
    }

    getIcon (type) {
        const classNames = classnames(styles.icon, this.getClassByType(type));
        return (<AUI.Icon icon={""} className={classNames} />)
    }

    getErrorRow(props) {
        return (
            <div className={styles.errorWrapper}>
                <AUI.Message type="error">
                    <div>
                        <h5 className={styles.title}>{props.title}</h5>
                        <p dangerouslySetInnerHTML={{__html: props.description}} />
                        {this.getLink(props.helpLink, props.helpLinkText, classnames("aui-button aui-button-primary", styles.errorButtonLink))}
                    </div>
                </AUI.Message>
            </div>
        );
    }

    getRow(props) {
        return (
            <div className={styles.wrapper}>
                    {this.getIcon(props.type)}
                    <h5 className={styles.title}>{props.title}</h5>
                    <p dangerouslySetInnerHTML={{__html: props.description}} />
                    {this.getLink(props.helpLink, props.helpLinkText)}
                    {props.children}
            </div>
        );
    }

    render () {
        const {type} = this.props;
        const isErrorOrFatal = type === 'error' || type === 'fatal';

        return isErrorOrFatal ? this.getErrorRow(this.props) : this.getRow(this.props);
    }
}


