import React, {PureComponent, PropTypes} from 'react';

export default class TableRow extends PureComponent {
    static propTypes = {
        rowChildren: PropTypes.arrayOf(PropTypes.node).isRequired,
        columnElement: PropTypes.node.isRequired
    };

    render () {
        return <tr>
            {this.props.rowChildren.map((rowChild, i) => (<this.props.columnElement key={i}>{rowChild}</this.props.columnElement>))}
        </tr>
    }
}


