export const translateAdditionalMarkup = i18n => markup => {
    return markup.replace(/\{\{([^}]+)\}\}/g, (_, key) => {
        return i18n(key);
    })
};

const mergeValueAndArgs = (value, args) => {
    return value.replace(/([^']|^)\{(\d+)\}/g, (_, pre, i) => {
        const index = Number(i);
        if (index >= args.length) {
            return pre;
        }
        return pre + args[Number(i)];
    });
};

export const translate = (map, key, ...args) => {
    if (!map) {
        if(process.env.NODE_ENV === 'development') {
            console.warn('[I18n]: translation service is not yet up and running');
        }
        return key;
    }
    if (!map[key]) {
        if(process.env.NODE_ENV === 'development') {
            console.warn('[I18n]: Key %s is not known', key);
        }
        return key;
    }

    if (args.length === 0) {
        return map[key];
    }

    return mergeValueAndArgs(map[key], args);
};


