import React, {PureComponent, PropTypes} from 'react';
import * as AUI from 'aui-react';
import classnames from 'classnames';

import styles from './pagecontent.less';

import Row, {propTypes as rowPropTypes} from '../Row/Row';
import {KB_LINK_START_STOP_JIRA} from '../../constants';

export const eventsByTypeProps = PropTypes.shape({
    error: PropTypes.arrayOf(PropTypes.shape(rowPropTypes)),
    warning: PropTypes.arrayOf(PropTypes.shape(rowPropTypes)),
    success: PropTypes.arrayOf(PropTypes.shape(rowPropTypes)),
});

export const statsProps = PropTypes.shape({
    total: PropTypes.number,
    errors: PropTypes.number,
    warnings: PropTypes.number,
    success: PropTypes.number,
    noErrors: PropTypes.bool,
    noWarnings: PropTypes.bool,
});

export default class PageContent extends PureComponent {
    static propTypes = {
        stats: statsProps.isRequired,
        eventsByType: eventsByTypeProps.isRequired,
        showAllChecks: PropTypes.bool,
        hasDismissibleWarnings: PropTypes.bool,
        canAcknowledgeWarnings: PropTypes.bool,
        acknowledgeWarnings: PropTypes.string,
    };

    static contextTypes = {
        i18n: PropTypes.func,
        refreshFn: PropTypes.func,
    };

    constructor(props) {
        super();
        this.state = {
            showAllChecks: props.showAllChecks || false,
        };
    }

    isLastRow(rowDatasets, i) {
        return (rowDatasets.length - 1) === i
    }

    renderRow(key, data, helpLinkText, children) {
        return (
            <Row
                key={key}
                title={data.title}
                description={data.description}
                type={data.type}
                helpLink={data.helpLink}
                helpLinkText={helpLinkText}
                eventId={data.eventId}
            >{children}</Row>
        );
    }

    renderErrorRows(dataSets) {
        if (dataSets.length === 0) {
            return null;
        }
        const {i18n} = this.context;

        const helpLinkText = i18n('johnson.check.events.event.kblink');
        return (
            <AUI.PageContent>
                <p dangerouslySetInnerHTML={{__html: i18n('johnson.page.headline.errors', `<a href="${KB_LINK_START_STOP_JIRA}" target="_blank">`, '</a>')}} />
                <div className={classnames(styles.errorRow, "error-row")}>
                    {dataSets.map((dataSet, i) => this.renderRow(i, dataSet, helpLinkText))}
                </div>
            </AUI.PageContent>
        );
    }

    renderWarningRows(dataSets, warningsAcknowledgeButton) {
        if (dataSets.length === 0) {
            return null;
        }
        const {i18n} = this.context;

        const helpLinkText = i18n('johnson.check.events.event.kblink');
        return (
            <AUI.PageContent>
                <p>{i18n('johnson.page.headline.warnings')}</p>
                <div className="warning-row">
                    {
                        dataSets.map((dataSet, i) => {
                            const ackButton = this.isLastRow(dataSets, i) ? warningsAcknowledgeButton : null;
                            return this.renderRow(i, dataSet, helpLinkText, ackButton);
                        })
                    }
                </div>
            </AUI.PageContent>
        );
    }

    renderSuccessRows(dataSets, showAllChecks) {
        if (dataSets.length === 0 || !showAllChecks) {
            return null;
        }
        const {i18n} = this.context;

        const helpLinkText = i18n('johnson.check.events.event.kblink');
        return (
            <div>
                {dataSets.map((dataSet, i) => this.renderRow(i, dataSet, helpLinkText))}
            </div>
        );
    }

    renderShowMoreButton(hasSuccessfulChecks, showAllChecks) {
        if (!hasSuccessfulChecks) {
            return null;
        }
        const {i18n} = this.context;

        return (
            <AUI.Button type="link" onClick={this.toggleAllChecks}>
                {i18n(!showAllChecks ? "johnson.check.events.show.all" : "johnson.check.events.show.less")}
            </AUI.Button>
        );
    }

    toggleAllChecks = () => {
        this.setState({
            showAllChecks: !this.state.showAllChecks,
        });
    };

    acknowledgeWarnings = () => {
        window.location = this.props.acknowledgeWarnings;
    };

    getAcknowledgeWarningsLink(hasDismissibleWarnings, canAcknowledgeWarnings, i18n) {
        if (!hasDismissibleWarnings) {
            return null;
        }

        if (!canAcknowledgeWarnings) {
            return null;
        }
        return (
            <div data-wd-id="acknowledge-warnings" className={classnames('aui-item', styles.acknowledgeButton, 'ack-button-wrapper')}>
                <AUI.Button
                    onClick={this.acknowledgeWarnings}>{i18n('johnson.check.acknowledge.warnings.cta')}</AUI.Button>
            </div>
        );
    }

    renderIgnoreWarningsCommandlineHint(hasDismissibleWarnings, canAcknowledgeWarnings) {
        if (!hasDismissibleWarnings) {
            return null;
        }

        if (canAcknowledgeWarnings) {
            return null;
        }

        const {i18n} = this.context;
        return (
            <div
                className={classnames('aui-item', styles.ignoreWarningsCLI)}
                dangerouslySetInnerHTML={{__html: i18n('johnson.warnings.ignore.cli', '<code>', '</code>')}}
                />
        );
    }

    render() {
        const {
            stats,
            eventsByType,
            hasDismissibleWarnings,
            canAcknowledgeWarnings,
        } = this.props;
        const {i18n} = this.context;
        const {
            showAllChecks
        } = this.state;
        const hasSuccessfulChecks = stats.success > 0;

        const warningsAcknowledgeButton = this.getAcknowledgeWarningsLink(hasDismissibleWarnings, canAcknowledgeWarnings, i18n);

        return (
            <div>
                {/* reimplement stats once they are available */}
                {this.renderErrorRows(eventsByType.error)}
                {this.renderWarningRows(eventsByType.warning, warningsAcknowledgeButton)}
                {this.renderSuccessRows(eventsByType.success, showAllChecks)}
                {this.renderShowMoreButton(hasSuccessfulChecks, showAllChecks)}
                {this.renderIgnoreWarningsCommandlineHint(hasDismissibleWarnings, canAcknowledgeWarnings)}
            </div>
        );
    }
}




