import React, {PureComponent, PropTypes} from 'react';
import * as AUI from 'aui-react';

import TableRow from './TableRow';

export default class Table extends PureComponent {
    static propTypes = {
        events: PropTypes.arrayOf(PropTypes.arrayOf(PropTypes.node)).isRequired,
    };

    static contextTypes = {
        i18n: PropTypes.func,
    };

    render () {
        const {i18n} = this.context;
        const {events} = this.props;
        return (
            <AUI.Table>
                <thead>
                    <tr>
                        <th width="40%">
                            {i18n('common.words.description')}
                        </th>
                        <th width="10%">
                            {i18n('common.words.time')}
                        </th>
                        <th>
                            {i18n('common.words.level')}
                        </th>
                        <th width="40%">
                            {i18n('common.words.exception')}
                        </th>
                    </tr>
                </thead>
                <tbody>
                {
                    events.map((event, i) => (
                        <TableRow rowChildren={event} key={i} columnElement="td" />
                    ))
                }
                </tbody>
            </AUI.Table>
        );
    }
}


